@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color:Beige
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.Mainsearch {
   padding: 130px;
   text-align: left;
   background-image: url(https://marvalero.github.io/prometeo/static/media/jose.a126c48d.png);
   background-size: 600px;
   background-repeat: no-repeat;
   background-position: right;
   background-color: Orange;
}

.Mainsearch-div {
   display:flex;
   flex-direction:column;
   flex-basis: 20%;
   display: inline-flex;
}

.Mainsearch-title {
   color: Orange;
   background: Black;
   font-size: 75px;
   line-height: 74px;
   font-weight: 700;
   margin: 0 5px 24px;
   float: left;
   padding: 10px;
   margin: 0 5px 24px;
   font-family: 'Libre Baskerville', cursive;
}

.Mainsearch-h2 {
   color: Beige;
   font-family: 'Open Sans', sans-serif;
   font-size: 30px;
   font-weight: 300;
   line-height: 32px;
   margin: 0 0 72px;
   text-align: center;
   }


.Mainsearch-p { color: #685206; font-family: 'Helvetica Neue', sans-serif; font-size: 14px; line-height: 24px; margin: 0 0 24px; text-align: justify; text-justify: inter-word; }

.Service {
   width: 60%;
   margin: 16px auto;
   border: 1px solid #eee;
   box-shadow: 0 2px 3px #ccc;
   padding: 16px;
   text-align: center;
}

